.modal {
  display: flex;
  /* visibility: hidden; */
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  padding: 0 19px;
  /* opacity: 0; */
  /* transition: visibility 0.5s, opacity 0.5s linear; */
  z-index: 999;
  background-color: rgba(0, 0, 0, 0.5);
  font-family: Cabnet Grotesk;
  font-size: 16px;
  line-height: 20px;
  font-weight: 799;
}

.modal__close {
  align-self: end;
  background-image: url("../images/close-icon.svg");
  border: none;
  width: 20px;
  height: 20px;
  cursor: pointer;
  background-repeat: no-repeat;
  background-size: contain;
  background-color: transparent;
}

.modal__content {
  display: flex;
  flex-direction: column;
  justify-content: center;
  max-width: 496px;
  width: 100%;
  margin: auto auto;
  padding: 28px 36px;
  /* position: relative; */
  border-radius: 20px;
  box-shadow: 0 0 0 25px rgba(0, 0, 0, 0.15);
  background-color: #fff;
}

.form {
  display: flex;
  flex-direction: column;
}

.form__input {
  border: none;
  border-bottom: solid 1px #000;
  /* height: 52px; */
}

.form__label:not(:first-of-type) {
  margin-top: 24px;
}

.form__input-radio + .form__label-radio {
  color: rgba(0, 0, 0, 0.5);
}

.form__input-radio:checked {
  accent-color: #000;
}

.form__input-radio:checked + .form__label-radio {
  color: #000;
}

.modal__button {
  width: 124px;
  height: 36px;
  padding: 8px 16px;
  background-color: #000;
  color: #fff;
}

.modal__button:hover {
  background-color: rgba(0, 0, 0, 0.6);
}

.modal__button:disabled {
  background-color: rgba(0, 0, 0, 0.6);
}

